import meteorologicalEchares from "./meteorologicalEchares.vue";
import { meteorological, temp_chart, devicename } from "../../utils/service";
import { formatDate } from "@/utils/common.js";

import mqtt from "mqtt";
export default {
  created() {
    this.initMqtt();
    const start = new Date();
    start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
    var s = formatDate(start, "yyyy-MM-dd hh:mm:ss");

    const date = new Date();
    var e =
      date.getFullYear() +
      "-" +
      ("0" + (date.getMonth() + 1)).slice(-2) +
      "-" +
      ("0" + date.getDate()).slice(-2) +
      " 23:59:59";
    this.timedata = [s, e];
  },
  components: {
    meteorologicalEchares
  },
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      datalist: "",
      datavalue1: "",
      echartsdata: "",
      echartsdata2: "",
      timedata: "",
      sdevicename: "", // 确认设备名称
      devicename: ""
    };
  },

  mounted() {
    this.getmetdata();
    this.changesdevicetype();
    this.initMqtt();
  },
  methods: {
    getmetdata() {
      meteorological({}, res => {
        this.datalist = res.data[0];
      });
    },
    topsure() {
      var startdate = "";
      var enddate = "";
      if (this.timedata) {
        for (let index = 0; index < this.timedata.length; index++) {
          startdate = this.timedata[0];
          enddate = this.timedata[1];
        }
      }

      var formData = {
        startdate: startdate,
        enddate: enddate,
        // attrname: this.sdeviceattr2,
        device_id: this.sdevicename
      };
      temp_chart(formData, res => {
        // this.echartsdata = res.data
        var datalist = res.data;
        var listpush = [];
        var listpush2 = [];
        datalist.map(item => {
          listpush.push([item.acquisitiontime, item.temp]);
          listpush2.push([item.acquisitiontime, item.humidity]);
        });
        this.echartsdata = listpush;
        this.echartsdata2 = listpush2;
      });
    },
    changesdevicetype() {
      var data = {
        devicetype: "tempmd"
      };
      devicename(data, res => {
        this.devicename = res.data;
      });
    },
    initMqtt(topic) {
      const mqttConfig = {
        port: 8083,
        keepalive: 60,
        clientId:
          "mqttjs_" +
          Math.random()
            .toString(16)
            .substr(2, 8)
        // protocolId: 'MQTT',
        // protocolVersion: 4,
        // clean: true,
      };
      var TopicList = {
        meteorological: "topic/mqtt/meteorological",
        devicestatistics: "topic/mqtt/devicestatistics",
        oilpoolstate: "topic/mqtt/oilpoolstate",
        smoke_check: "topic/mqtt/smoke_check"
      };
      const _this = this;
      //建立连接，需要搭建消息中间件服务器的（我这里是后台）提供用户名或密码
      const cfg = Object.assign(mqttConfig, {
        // username: 'xxxx1',
        // password: 'xxx2',
      });
      // 在data中定义MQTT,以便断开连接
      // ws://xxxxx是连接地址，后台提供，wss是https连接
      _this.MQTT = mqtt.connect("ws://8.142.173.95:8083/mqtt", cfg);
      _this.MQTT.on("error", e => {
        _this.MQTT.end();
      });
      //建立连接后订阅主题
      _this.MQTT.on("connect", () => {
        // 订阅一个主题
        for (let k in TopicList) {
          const element = TopicList[k];
          _this.MQTT.subscribe(
            element,
            {
              qos: 1
            },
            err => {
              if (!err) {
                // console.info(" ---- 订阅成功");
              } else {
                // console.warn("订阅失败");
              }
            }
          );
        }
      });
      // 后台发送的消息
      _this.MQTT.on("message", function(top, message) {
        //发送过来的如果是对象你是需要解析的。
        const res = JSON.parse(message.toString());
        const TOP = top;
        // console.log(top + ": ", res);
        //根据不同主题进行赋值操作
        switch (TOP) {
          case "topic/mqtt/meteorological":
            var slist = res.data[0];
            _this.$nextTick(() => {
              _this.datalist.temp = slist.temp;
              _this.datalist.humidity = slist.humidity;
              _this.datalist.rainfall = slist.rainfall;
              _this.datalist.pressure = slist.pressure;
              _this.datalist.sunshine = slist.sunshine;
              _this.datalist.windspeed = slist.windspeed;
              _this.datalist.winddirection = slist.winddirection;
              _this.datalist.oneminuteaws = slist.oneminuteaws;
              _this.datalist.oneminuteawd = slist.oneminuteawd;
              _this.datalist.tenminuteaws = slist.tenminuteaws;
              _this.datalist.tenminuteawd = slist.tenminuteawd;
              _this.datalist.tenminutemws = slist.tenminutemws;
            });
            break;
          case "topic/mqtt/devicestatistics":
            break;
          case "topic/mqtt/oilpoolstate":
            break;
          case "topic/mqtt/smoke_check":
            break;
        }
      });
      // 其他异常事件
      _this.MQTT.on("reconnect", () => {
        // console.info("正在重连");
      });
      _this.MQTT.on("disconnect", error => {
        // console.info("服务器断开:", error);
      });
      _this.MQTT.on("close", () => {
        _this.MQTT.end();
      });
    }
  },
  beforeDestroy() {
    if (this.MQTT.end) this.MQTT.end();
  }
};



