<template>
    <div class="meteorological">
        <div class="meteorological_top">
            <div class="meteorological_item1 bg">
                <div class="temp fc">
                    <div class="temp_img1 station-imgpostion  img"></div>
                    <div class="meteorological_item1_title fsz">温度</div>
                    <div class="num">{{ datalist.temp }}℃</div>
                </div>
                <div class="hum fc">
                    <div class="temp_img2 station-imgpostion  img"></div>
                    <div class="meteorological_item1_title fsz">湿度</div>
                    <div class="num">{{ datalist.humidity }}%</div>
                </div>
                <div class="rainfall fc">
                    <div class="temp_img3 station-imgpostion  img"></div>
                    <div class="meteorological_item1_title fsz">雨量</div>
                    <div class="num">{{ datalist.rainfall }}mm</div>
                </div>
                <div class="airpressure fc">
                    <div class="temp_img4 station-imgpostion  img"></div>
                    <div class="meteorological_item1_title fsz">气压</div>
                    <div class="num">{{ datalist.pressure }}hPa</div>
                </div>
                <div class="rizhao fc">
                    <div class="temp_img5 station-imgpostion  img"></div>
                    <div class="meteorological_item1_title fsz">日照</div>
                    <div class="num">{{ datalist.sunshine }}W/m²</div>
                </div>
            </div>
            <div class="meteorological_item2 bg">
                <div class="meteorological_item2_left">
                    <div class=" fc">
                        <div class="temp_img6 station-imgpostion  meteorological_item2_img img"></div>
                        <div class="meteorological_item2_title fsz">风速</div>
                        <div class="meteorological_item2_num">{{ datalist.windspeed }}m/s</div>
                    </div>
                    <div class=" fc">
                        <div class="temp_img7 station-imgpostion  meteorological_item2_img img"></div>
                        <div class="meteorological_item2_title fsz">风向</div>
                        <div class="meteorological_item2_num">{{ datalist.winddirection }}°</div>
                    </div>
                </div>
                <div class="meteorological_item2_right">
                    <div class="meteorological_title fsz"><span>1分钟平均风速</span> <span>-------------------</span>
                        <span>{{ datalist.oneminuteaws }}m/s</span>
                    </div>
                    <div class="meteorological_title fsz"><span>1分钟平均风向</span> <span>-------------------</span>
                        <span>{{ datalist.oneminuteawd }}°</span>
                    </div>
                    <div class="meteorological_title fsz"><span>10 分钟平均风速</span> <span>-------------------</span>
                        <span>{{ datalist.tenminuteaws }}m/s</span>
                    </div>
                    <div class="meteorological_title fsz"><span>10 分钟平均风向</span> <span>-------------------</span>
                        <span>{{ datalist.tenminuteawd }}°</span>
                    </div>
                    <div class="meteorological_title fsz"><span>10 分钟最大风速</span> <span>-------------------</span>
                        <span>{{ datalist.tenminutemws }}m/s</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="meteorological_item3 bg">
            <div class="title">
                <span></span>
                <div class="title_name">温度与湿度曲线分布图</div>
            </div>
            <span class="fsz etime">检测时间：</span>
            <el-date-picker v-model="timedata" type="datetimerange" align="right" start-placeholder="开始日期"
                value-format="yyyy-MM-dd HH:mm:ss" end-placeholder="结束日期" :default-time="['00:00:00', '23:59:59']"
                :picker-options="pickerOptions">
            </el-date-picker>
            <!-- <div> -->
            <span class="etime fsz"> 设备名称：</span>
            <el-select v-model="sdevicename" clearable placeholder="请选择">
                <el-option v-for="(item, i) in devicename" :key="i" :label="item.device_name" :value="item.device_id">
                </el-option>
            </el-select>
            <el-button type="success" plain size="medium" class="button" @click="topsure">确认</el-button>
            <!-- </div> -->
            <meteorologicalEchares :echartsdata='echartsdata' :echartsdata2='echartsdata2' v-if="echartsdata" />
        </div>
    </div>
</template>

<script>
import meteorological from "./meteorological";
export default meteorological;
</script>
<style scoped>
@import url("meteorological.css");
@import url("../../assets/css/common.css");
</style>


