<template>
    <div>
        <div id="vibration-Chart" style="height: 420px;"></div>
    </div>
</template>

<script>
import echartMixins from "@/echartsUtils/resizeMixins";
export default {
    data() {
        return {
            chart: null
        };
    },
    mixins: [echartMixins],
    props: {
        echartsdata: Array,
        echartsdata2: Array
    },
    watch: {
        echartsdata(newValue, oldValue) {
            this.draw();
        },
    },
    mounted() {
        this.draw();
    },

    methods: {
        draw() {
            // 基于准备好的dom，初始化echarts实例
            this.chart = this.$echarts.init(document.getElementById("vibration-Chart"));
            //  ----------------------------------------------------------------
            //  ----------------------------------------------------------------
            var option;
            option = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        lineStyle: {
                            color: {
                                type: 'linear',
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                colorStops: [{
                                    offset: 0,
                                    color: 'rgba(0, 255, 233,0)'
                                }, {
                                    offset: 0.5,
                                    color: 'rgba(255, 255, 255,1)',
                                }, {
                                    offset: 1,
                                    color: 'rgba(0, 255, 233,0)'
                                }],
                                global: false
                            }
                        },
                    },
                },
                legend: {
                    icon: 'circle',
                    // top: '15%',
                    right: '3%',
                    button: '30%',
                    itemWidth: 30,
                    itemGap: 5,
                    textStyle: {
                        color: '#fff',
                        padding: [0, 30, 0, 0],
                    },
                },
                xAxis:
                {
                    // data: dateList,
                    show: true,

                    type: 'time',
                    boundaryGap: false, //从Y轴出发，这个false很好的
                    axisLine: {
                        show: true, //显示x坐标轴轴线
                        lineStyle: {
                            color: 'rgba(255,255,255,.4)',
                        },
                    },
                    axisTick: {
                        show: false, //不显示x坐标1cm刻度
                    },
                    axisLabel: {
                        color: '#ffffff', //x轴label文字颜色
                    },
                    splitLine: {
                        show: true,
                        lineStyle: {
                            color: 'rgba(52, 51, 51, 0.8)',
                        },
                    },
                },
                yAxis:
                {
                    type: 'value',

                    boundaryGap: false, //从Y轴出发，这个false很好的
                    axisLine: {
                        show: true, //显示x坐标轴轴线
                        lineStyle: {
                            color: 'rgba(255,255,255,.4)',
                        },
                    },
                    axisTick: {
                        show: false, //不显示x坐标1cm刻度
                    },
                    axisLabel: {
                        color: '#ffffff', //x轴label文字颜色
                    },
                    splitLine: {
                        show: true,
                        lineStyle: {
                            color: 'rgba(52, 51, 51, 0.8)',
                        },
                    },
                },
                grid: {
                    top: '5%',
                    left: '5%',
                    right: '5%',
                    bottom: '5%',
                    containLabel: true
                },
                series: [
                    {
                        name: '温度',
                        type: 'line',
                        showSymbol: false,
                        // smooth: true, //是否平滑
                        lineStyle: {
                            color: 'rgba(255, 214, 0, 1)'
                        },
                        label: {
                            show: false
                        },
                        itemStyle: {
                            color: 'rgba(255, 214, 0, 1)',
                            borderColor: 'rgba(255, 214, 0, 1)',
                            borderWidth: 1
                        },
                        areaStyle: {
                            color: {
                                type: "linear",
                                x: 0,
                                y: 1,
                                x2: 0,
                                y2: 0,
                                colorStops: [
                                    {
                                        offset: 0,
                                        color: "rgba(255, 214, 0, 0)" // 0% 处的颜色
                                    },
                                    {
                                        offset: 1,
                                        color: "rgba(255, 214, 0, 1)" // 100% 处的颜色
                                    }
                                ],
                                globalCoord: false // 缺省为 false
                            },
                        },
                        data: this.echartsdata
                    },
                    {
                        name: '湿度',
                        type: 'line',
                        showSymbol: false,
                        // smooth: true, //是否平滑
                        lineStyle: {
                            color: 'rgba(66, 194, 235, 1)'
                        },
                        label: {
                            show: false
                        },
                        itemStyle: {
                            color: 'rgba(66, 194, 235, 1)',
                            borderColor: 'rgba(66, 194, 235, 1)',
                            borderWidth: 1
                        },
                        areaStyle: {
                            color: {
                                type: "linear",
                                x: 0,
                                y: 1,
                                x2: 0,
                                y2: 0,
                                colorStops: [
                                    {
                                        offset: 0,
                                        color: "rgba(66, 194, 235, 0)" // 0% 处的颜色
                                    },
                                    {
                                        offset: 1,
                                        color: "rgba(66, 194, 235, 1)" // 100% 处的颜色
                                    }
                                ],
                                globalCoord: false // 缺省为 false
                            },
                        },
                        data: this.echartsdata2
                    }

                ]
            };
            // option && chart.setOption(option);
            this.chart.setOption(option);
        }
    },
    destroyed() {
        window.onresize = null;
    }
};
</script>

<style>
</style>


